import { Paper, Tab, Tabs, Typography } from "@mui/material";
import React, { useState } from "react";
import GenerateGrid from "../features/generateGrid";
import Fade from "@mui/material/Fade";
import { Container } from "@mui/material";
import { styled as styled1 } from "@mui/material/styles";
import BounceText from "../animations/Bounce";



export default function Projects(props) {

// const bounceAnimation = keyframes`${bounce}`;
// const Bounce = styled.div`
// animation: 1s ${bounceAnimation};
// `;
  const StyledTab = styled1((props) => <Tab disableRipple {...props} />)(
    ({ theme }) => ({
      textTransform: "none",
      fontSize: 17,
      marginRight: theme.spacing(1),
      "&:hover": {
        color: "#2C5364",
        opacity: 1,
      },
      "&.Mui-selected": {
        color: "white",
        backgroundColor: "#203A43",
      },
    })
  );

  const [value, setValue] = useState("ALL");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <React.Fragment>
      <div
        className="jumbotron jumbotron-fluid"
        style={{
          background: "linear-gradient(to right, #0f2027, #203a43, #2c5364)",
          paddingTop: 80,
          marginBottom:0
        }}
      >
        {/*  */}
        
          <div className="container" >
            <BounceText/>

            <Fade in timeout={1000}>
              <div>
            <Typography variant="h3" sx={{ color: "lightgray" }}>
              I am a{" "}
              <span style={{ color: "white" }}>
                <strong>full-stack</strong>
              </span>{" "}
              data scientist.
            </Typography>

            
            <Typography variant="h6" style={{ color: "lightgray" }}>
              I build scalable and impactful end-to-end data science and machine learning solutions.
            </Typography>
            </div>
            </Fade>
            
          </div>
          
        {/* </Fade> */}
      </div>

      <div className="jumbotron jumbotron-fluid" style={{marginBottom:0, paddingTop:30, background:'linear-gradient(to right, #ece9e6, #ffffff)'}}>
        <div className="container" style={{ paddingBottom: 10}}>
          
          <Paper variant="outlined" style={{marginBottom:15}}>
            <Tabs
              value={value}
              scrollButtons="auto"
              allowScrollButtonsMobile
              centered
              onChange={handleChange}
              TabIndicatorProps={{ style: { backgroundColor: "#203A43" } }}
              sx={{
                
                "& .MuiTabs-flexContainer": {
                  flexWrap: "wrap",
                },
              }}
            >
              <StyledTab label="All" value="ALL"/>
              <StyledTab label="Machine Learning" value="ML" />
              <StyledTab label="Big Data Analytics" value="BD" />
              <StyledTab label="Data Mining" value="DMW" />
            </Tabs>
          </Paper>
        </div>
        <Container maxWidth="xl" >
          <GenerateGrid tag={value}></GenerateGrid>
        </Container>
      </div>
    </React.Fragment>
  );
}
